import { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBars, faRectangleXmark } from '@fortawesome/free-solid-svg-icons';
import cls from '../Navbar.module.scss';
import { Link, useNavigate } from 'react-router-dom';
import { isMobile } from 'react-device-detect';
import { FLOWHERO } from '../../../../shared/utility';
import { resetApplication } from '../../../../services/auth.service';
import { Button } from '../../../common';

export const Navbar = props => {
	const { background = 'white' } = props;
	const [nav, setNav] = useState(false);
	const navigate = useNavigate();


	const pricingRef = document.getElementById('ourpricing')

	const openNav = () => {
		setNav(!nav);
	};

	return (
		<div className={cls.navbarsection} style={{ position: 'fixed', backgroundColor: background }}>
			<div className={cls.navbartitle} style={{ fontSize: isMobile ? '20px' : '26px', marginLeft: isMobile ? '0' : '50px' }}>
				{FLOWHERO(isMobile ? '26px' : '28px', 'var(--app-color-gray-dark)', 'bold')}
			</div>
			{/* Desktop */}
			<ul className={cls.navbaritems} style={{ display: isMobile ? 'none' : '' }}>
				<li>
					<Link to="/" className={cls.navbarlinks} style={{ cursor: 'pointer', 
						fontWeight:'bold' 
						}}>
						{`HOME`}
					</Link>
				</li>
				<li>
					<a
						className={cls.navbarlinks}
						style={{ cursor: 'pointer', 
							fontWeight:'bold' 
						}}
						onClick={() => {
							window.scrollTo({ top: document.body.scrollHeight / 10, behavior: 'smooth' })
						}}
					>
						{'SOLUTIONS'}
					</a>
				</li>
				<li>
					<a
						className={cls.navbarlinks}
						style={{ cursor: 'pointer',
							 fontWeight:'bold'
							 }}
						onClick={() => {
							
							window.scrollTo({ top: document.body.scrollHeight / 1.44, behavior: 'smooth' })
						}}
					>
						{'LIBRARIES'}
					</a>
				</li>
				<li>
					<a
						className={cls.navbarlinks}
						style={{ cursor: 'pointer', 
							fontWeight:'bold'
						 }}
						onClick={() => {
							window.scrollTo({ top: document.body.scrollHeight / 1.21, behavior: 'smooth' })
						}}
					>
						{'PLANS'}
					</a>
				</li>
				
				<li>
					<a href="#/register" 
					className={cls.navbarlinks}
					style={{ cursor: 'pointer', 
						fontWeight:'bold'
					 }}
					>
						{`REGISTER`}
					</a>
				</li>
				<li>
					<Button title={'LOGIN'}
						style={{ marginTop: '20px', backgroundColor: background }}
						titleStyle={{
							fontFamily: 'var(--app-text-main-font)',
							color: background === 'transparent' ? '#008001':  'orange',
							fontWeight: 'bold',
							fontSize: '14px',
							letterSpacing: '.8px'

						}}
						light
						clicked={() => {
							resetApplication();
							navigate('/login');
						}}
					/>

				</li>
			</ul>

			{/* Mobile */}
			<div className={`mobile-navbar ${nav ? 'open-nav' : ''}`} style={{ width:'80%',height: '100vh', backgroundColor: '', color: 'black', justifyContent: 'center', alignItems: 'flex-start' }}>
				<div onClick={openNav} className="mobile-navbar-close">
					<FontAwesomeIcon icon={faRectangleXmark} className="hamb-icon" style={{ color: 'var(--app-color-gray)', width: '25px', height: '25px' }} />
				</div>

				<ul className={cls.mobilenavbarlinks} >
					<li>
						<Link onClick={openNav} to="/" style={{ color: 'black' }}>
							{`HOME`}
						</Link>
					</li>

					<li>
						<a href="#/featured/sports" className={cls.navbarlinks} style={{ color: 'black' }}>
							{`PALARO`}
						</a>
					</li>

					<li>
						<a onClick={openNav} href="/#/login" style={{ color: 'black' }}>
							{`LOGIN`}
						</a>
					</li>

					<li>
						<a onClick={openNav} href="/" style={{ color: 'black' }}>
							{`REGISTER`}
						</a>
					</li>
				</ul>
			</div>

			{/* Hamburger Icon */}
			<div className="mobile-nav" style={{ display: isMobile ? 'block' : 'none' }}>
				<FontAwesomeIcon icon={faBars} onClick={openNav} className="hamb-icon" />
			</div>
		</div>
	);
}

export default Navbar;
