import React, { useEffect, useState } from 'react';
import Navbar from '../sub/Flowhero/Navbar';
import Info from '../sub/Flowhero/Info';
import Hero, { OurLibraries, OurMarkets, WeDeliverHRIS } from '../sub/Flowhero/HeroF';
import OurSolutions from '../sub/OurMarket';
import { useDispatch } from 'react-redux';
import { setShowMenu, setShowSideMenu } from '../../../reduxStore/actions/flowActions';
import './Home.css';
import { isMobile } from 'react-device-detect';

function Flowhero() {
	const [background, setBackground] = useState('transparent');
	document.title = 'Flowhero';

	const dispatch = useDispatch();

	const handleNavigation = e => {
		const window = e.currentTarget;
		if (window.scrollY == 0) {
			setBackground('transparent')
		} else {
			setBackground(isMobile ? 'white':'#F6F4EC')
		}
	};

	useEffect(() => {
		window.addEventListener('scroll', e => handleNavigation(e));
		window.scrollTo({ top: 0 });
	}, []);

	useEffect(() => {
		dispatch(setShowSideMenu(false));
		dispatch(setShowMenu(false));
	}, []);

	return (
		<div className="home-section">
			<Navbar background={background} />
			<Hero />
			<OurSolutions/>
			<WeDeliverHRIS />
			<OurMarkets />
			<OurLibraries />
			<Info />
		</div>
	);
}

export default Flowhero;
